import React from 'react'
import { Icon,SidebarContainer,CloseIcon,SidebarMenu,SidebarLink,SideBtnWrap,SidebarRoute} from './SidebarElements';

 const Sidebar = ({isOpen,toggle}) => {
    return (
        <SidebarContainer isOpen={isOpen}onClick={toggle}>
           <Icon onClick={toggle} >
               <CloseIcon/>
           </Icon> 
           <SidebarMenu>
               <SidebarLink to="/">Kids Kicks</SidebarLink>
               <SidebarLink to="/">Male Kicks</SidebarLink>
               <SidebarLink to="/">Female Kicks</SidebarLink>
           </SidebarMenu>
           <SideBtnWrap>
               <SidebarRoute to="/">Order Now </SidebarRoute>
           </SideBtnWrap>
        </SidebarContainer>
    )
}
export default Sidebar;